import React from "react";
import { Alert, Button } from "reactstrap";
import AudioAnalyser from "./AudioAnalyser";
import { resolve } from "url";

const shortid = require("shortid");

const BASE_URL = "https://candicesurvey2020.herokuapp.com";

function sleep(milliseconds) {
  const date = Date.now();
  let currentDate = null;
  do {
    currentDate = Date.now();
  } while (currentDate - date < milliseconds);
}

function Send(props) {
  let send_props = props.url_id
    ? { text: props.url_id, color: "success" }
    : { text: "Send", color: "warning" };
  let purl = resolve(BASE_URL, send_props.text);
  if (props.status === "success") {
    return (
      <Alert key="send-success" color="success">
        Find your recording at
        <br></br>
        <a href={purl} target="_blank" rel="noopener noreferrer">
          {purl}
        </a>
      </Alert>
    );
  } else if (props.status === "uploading") {
    return (
      <Alert key="send-uploading" color="warning">
        Your recording at will soon be available at
        <br></br>
        <a href={purl} target="_blank" rel="noopener noreferrer">
          {purl}
        </a>
      </Alert>
    );
  } else if (props.status === "failed") {
    return (
      <Alert key="send-fail" color="danger">
        The upload has failed!{" "}
        <span role="img" aria-label="sad">
          ðŸ˜¢
        </span>
      </Alert>
    );
  } else {
    return (
      <Button
        key="send-button"
        color={send_props.color}
        size="lg"
        style={{ minWidth: 20, marginRight: 10 }}
        onClick={props.onClick}
      >
        {send_props.text}
      </Button>
    );
  }
}

class RecordToggle extends React.Component {
  constructor(props) {
    super(props);

    let recorder = navigator.mediaDevices
      .getUserMedia({ audio: true })
      .then((stream) => {
        let mediaRecorder = new MediaRecorder(stream);
        mediaRecorder.onstop = this.recorderOnStop;
        mediaRecorder.onstart = this.recorderOnStart;
        mediaRecorder.ondataavailable = this.recorderOnDataAvailable;
        return mediaRecorder;
      });

    this.state = {
      isToggleOn: false,
      url_id: "",
      chunks: [],
      blob: null,
      sendStatus: "",
      mediaRecorder: recorder,
    };
    // This binding is necessary to make `this` work in the callback
    this.handleClick = this.handleClick.bind(this);
    this.handleSend = this.handleSend.bind(this);
    this.handleDelete = this.handleDelete.bind(this);
    this.recorderOnStop = this.recorderOnStop.bind(this);
    this.recorderOnStart = this.recorderOnStart.bind(this);
    this.recorderOnDataAvailable = this.recorderOnDataAvailable.bind(this);
  }

  recorderOnStop(event) {
    // let clipName = prompt(
    //   "Please enter your participant ID (or leave blank if you don't remember)"
    // );
    // console.log(clipName);
    let blob = new Blob(this.state.chunks, {
      type: "audio/ogg; codecs=opus",
    });
    this.setState({ blob });
  }
  recorderOnDataAvailable(event) {
    let chunks = [];
    chunks.push(...this.state.chunks);
    chunks.push(event.data);
    this.setState({ chunks });
  }
  recorderOnStart(event) {
    this.setState(function (state) {
      return { chunks: [], blob: null };
    });
  }

  handleClick() {
    if (!this.state.isToggleOn) {
      this.state.mediaRecorder.then((m) => {
        if (m.state !== "recording") {
          m.start();
          console.log("mediaRecorder status: " + m.state);
        } else {
          console.info("mediaRecorder is already recording");
        }
      });
    } else {
      this.state.mediaRecorder.then((m) => {
        if (m.state !== "inactive") {
          m.stop();
          console.log("mediaRecorder status: " + m.state);
        } else {
          console.info("mediaRecorder is already inactive");
        }
      });
    }

    this.setState({
      isToggleOn: !this.state.isToggleOn,
      mediaRecorder: this.state.mediaRecorder,
    });
  }
  handleSend() {
    const uid = shortid.generate();
    const purl = process.env.PUBLIC_URL + "/recording/" + uid;
    console.log("Uploading to " + purl);

    let blob = new Blob(this.state.chunks, {
      type: "audio/ogg; codecs=opus",
    });
    console.log("Data size: ", blob.size);
    this.setState({ url_id: purl, sendStatus: "uploading" });

    let reader = new window.FileReader();
    reader.readAsDataURL(blob);
    let _this = this;
    reader.onloadend = function () {
      let base64 = reader.result;
      base64 = base64.split(",")[1];
      // console.log(base64);

      const requestOptions = {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ uid: uid, audio: base64 }),
      };
      fetch(purl, requestOptions)
        .then(async (response) => {
          _this.setState({ sendStatus: "uploading" });
          if (!response.ok) {
            _this.setState({ sendStatus: "failed" });
            return Promise.reject(response.status);
          }
          // sleep(3000);
          _this.setState({ sendStatus: "success" });
          // const data = await response.json();
          // if (data && data.message) {
          //   this.setState({ postId: data.id });
          // }
        })
        .catch((error) => {
          _this.setState({ sendStatus: "failed" });
          console.error("There was an error uploading the file!\n", error);
        });
    };
    console.log(this.state.sendStatus);
  }
  handleDelete() {
    this.setState({ isToggleOn: false, blob: null, url_id: "" });
  }

  render() {
    let record_props = this.state.isToggleOn
      ? { text: "Stop recording", color: "danger" }
      : { text: "Record", color: "primary" };
    let send_props = this.state.url_id
      ? { text: this.state.url_id, color: "success" }
      : { text: "Send", color: "warning" };
    if (!this.state.blob) {
      return (
        <div className="text-center">
          <Button
            color={record_props.color}
            size="lg"
            style={{ minWidth: 200 }}
            onClick={this.handleClick}
          >
            {record_props.text}
          </Button>
        </div>
      );
    } else {
      return (
        <div className="text-center">
          <Button
            color={record_props.color}
            size="lg"
            style={{ minWidth: 200 }}
            onClick={this.handleClick}
          >
            {record_props.text}
          </Button>
          <br></br>
          <br></br>
          <div className="text-center">
            <audio
              style={{ minWidth: 320 }}
              controls={true}
              src={URL.createObjectURL(this.state.blob)}
            ></audio>
          </div>
          <Send
            status={this.state.sendStatus}
            url_id={this.state.url_id}
            onClick={this.handleSend}
          ></Send>
        </div>
      );
    }
  }
}

export default RecordToggle;

